:root {
  --primary-color: #6a11cb;
  --secondary-color: #2575fc;
  --tertiary-color: #ff516e;
  --background-color: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  --text-color: #333;
  --card-background: linear-gradient(135deg, #fff 0%, #f1f2f3 100%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: var(--background-color);
  color: var(--text-color);
  padding-top: 80px;
  font-family: Times New Roman, Times, serif;
  line-height: 1.6;
}

.portfolio-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

.navbar {
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  z-index: 1000;
  justify-content: center;
  width: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 10px #0000001a;
}

.navbar button {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  padding: 15px 20px;
  font-family: Times New Roman, Times, serif;
  font-size: 1.1rem;
  font-weight: 500;
  transition: all .3s;
}

.navbar button:hover, .navbar button.active {
  background: #fff3;
  transform: scale(1.05);
}

.navbar button.active {
  background: #ffffff4d;
  border-bottom: 3px solid #fff;
  font-weight: bold;
  transform: scale(1.1);
}

h2 {
  color: var(--primary-color);
  text-align: left;
  text-transform: uppercase;
  margin-bottom: 5px;
  padding-left: 20px;
  font-family: Times New Roman, Times, serif;
  font-size: 2.5rem;
}

.section {
  flex-direction: column;
  justify-content: center;
  min-height: 70vh;
  padding: 20px;
  display: block;
  position: relative;
}

.section-divider {
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color), var(--tertiary-color));
  width: 100%;
  height: 5px;
  position: absolute;
  top: 0;
  left: 0;
}

.about-content {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.about-card {
  background: var(--card-background);
  border-radius: 10px;
  width: 100%;
  max-width: 1000px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 6px 20px #0000001a;
}

.about-image {
  flex: 0 0 300px;
}

.about-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.about-text {
  flex: 1;
  align-items: center;
  padding: 30px;
  display: flex;
}

.experience-container, .projects-container, .publications-container, .certifications-container {
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.experience-card, .project-card, .publication-card, .certification-card {
  background: var(--card-background);
  border-radius: 10px;
  flex: 1;
  transition: transform .3s, box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 6px 20px #0000001a;
}

.experience-header, .project-header, .publication-header {
  background: #0000000d;
  padding: 15px;
}

.experience-content, .project-content, .publication-content {
  padding: 15px;
}

.experience-card:hover, .project-card:hover, .publication-card:hover, .certification-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 30px #00000026;
}

.experience-image, .certification-image {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.contact .social-links {
  justify-content: center;
  gap: 50px;
  display: flex;
}

.social-link {
  color: var(--primary-color);
  font-size: 3rem;
  transition: color .3s;
}

.social-link:hover {
  color: var(--secondary-color);
}

@media (width <= 1024px) {
  .experience-container, .projects-container, .publications-container, .certifications-container {
    flex-direction: column;
  }

  .experience-card, .project-card, .publication-card, .certification-card {
    flex: 0 100%;
  }

  .about-card {
    text-align: center;
    flex-direction: column;
  }

  .about-image {
    flex: none;
    max-width: 300px;
    margin: 0 auto;
  }

  .contact .social-links {
    gap: 30px;
  }
}
